'use strict'

const roulette = document.querySelector('.roulette')
const images = document.querySelectorAll('.slot__images')
const slot = document.querySelectorAll('.slot')
const btn = document.querySelector('.start')
const segment = document.querySelector('.slot__segment')
const cost = document.querySelector('.money__cost')
const bet = document.querySelector('.bet__input')
const text = document.querySelector('.text')

console.log(slot.clientHeight);


btn.addEventListener('mousedown', () => {
    new Audio('../audio/press.mp3').play()
})
btn.addEventListener('mouseup', () => {
    new Audio('../audio/realese.mp3').play()
})

function changePosition(el, pos, time) {
    el.animate(
        [
            {top: '0'},
            {top: `${-pos}px`}
        ],
        {
            duration: time
        }
    )
    el.style.top = `${-pos}px`
}

function getPos(q) {
    let num = Math.round(((Math.random() * q)))
    let pos = slot[0].clientHeight + slot[0].clientHeight * (num - 1)
    return [num, pos + (slot[0].clientHeight * 10 - slot[0].clientHeight)]
}

btn.addEventListener('click', () => {
    console.log(slot[0].clientHeight);
    let money = parseInt(cost.textContent)
    let value = parseInt(bet.value)



    if (value > money) {
        text.innerHTML = 'Bet is more than the balance'
    }

    if (!money) {
        text.innerHTML = 'No money on balanse'
    }

    if (value <= money && money) {
        text.innerHTML = 'Spinning...'
        money -= value
        cost.innerHTML = money

        const quantifier = 25
        btn.classList.add('start_disactive')
        btn.disabled = true
        new Audio('../audio/ticking.mp3').play()
        let nums = []
        
        images.forEach(el => {
            let pos = getPos(quantifier)
            let coor = pos[1]
            nums.push(pos[0] - (5 * Math.floor(pos[0] / 5)))

            if (coor > images[0].clientHeight) {
                for (let i = 1; i <= quantifier / 5; i++) {
                    images.forEach(el => el.append(segment.cloneNode(true)))
                }
            }

            changePosition(el, coor, 2000)
        })


        setTimeout(() => {
            if (new Set(nums).size == 1) {
                confetti({
                    particleCount: 170,
                    spread: 100,
                    origin: {y: 0.8}
                })
                if (nums.includes(1)) {
                    text.innerHTML = 'JACKPOT'
                    money += value * 3
                } else {
                    text.innerHTML = 'You are win'
                    money += value * 2
                }
                cost.innerHTML = money
                new Audio('../audio/win.mp3').play()
            } else {
                new Audio('../audio/end.mp3').play()
                text.innerHTML = 'You are lose'
            }
            btn.classList.remove('start_disactive')
            btn.disabled = false
            console.log(nums);
            
        }, 2000)

    } else {
        console.log('bet');
    }
})
